import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
    state:{ //
        city: '',
        index: 0
    },
    actions:{ //
        //ctxmutationscity
        //commitcitychangeCity
        changeCity(ctx,city){
            ctx.commit('changeCity',city);
        }
    },
    mutations:{ //
        changeCity(state,city){
            state.city = city;
        }
    }
})


